import styled from "styled-components";
import { pallete } from "../../../theme";

export const Style = styled.div`
.content {
  .items {
    .item {
      .item-link {
        color: ${pallete.primaryColor};
        font-size: 1.6rem;
        font-weight: 600;
        transition: all 0.3s ease-in-out;
        &:hover {
          color: ${pallete.uiColor};
        }
      }
    }
  }
}
`;