import styled from "styled-components";
import { pallete } from "../../../theme";

export const Style = styled.div`
padding: 32px 0;
border-bottom: 1px solid ${pallete.primaryColor};
.persona {
  width: 100%;
  .col {
    width: 50%;
    .item {
      width: 100%;
      .item-title {
        width: 20%;
        min-width: 100px;
        font-size: 1.6rem;
        font-weight: 600;
        color: ${pallete.primaryColor};
      }
      .item-body {
        font-size: 1.4rem;
        font-weight: 500;
        color: ${pallete.primaryColor};
        opacity: 0.6;
      }
    }
  }
}
@media only screen and (max-width: 900px) {
  .persona {
    flex-direction: column;
    align-items: center;
    width: 100%;
    gap: 10px;
    .col {
      width: 100%;
      align-items: center;
      justify-content: space-evenly;
      .item {
        width: 100%;
        .item-title {
          width: 50%;
        }
      }
    }
  }
}
`;