import { Link } from "react-router-dom";
import { Style } from "./style";
import { useEffect, useRef, useState } from "react";
import { Sidebar } from "./sidebar";


export const Navbar: React.FC = () => {
  const [openMenu, setOpenMenu] = useState<boolean>(false);
  const refElement = useRef<HTMLDivElement>(null);
  useEffect(() => {
    const handleClickOutside = (event: MouseEvent | TouchEvent) => {
      if (openMenu && refElement.current && !refElement.current.contains(event.target as Node)) {
        setOpenMenu(false);
      }
    };
    document.addEventListener('mouseup', handleClickOutside);
    document.addEventListener('touchend', handleClickOutside);
    return () => {
      document.removeEventListener('mouseup', handleClickOutside);
      document.removeEventListener('touchend', handleClickOutside);
    };
  }, [openMenu, setOpenMenu]);
  return (
    <Style>
      <div className="container">
        <div className="content flex aling-center justify-between">
          <div className="logo">
            <Link className="image flex align-center justify-center" to={'/'}>
              <img className="img" src="/asset/image/logo.svg" alt="Hamed Qaradaqi" />
            </Link>
          </div>
          <div className="hamburger-menu flex align-center justify-center" onClick={() => setOpenMenu(!openMenu)}>
            <div className={`${!openMenu ? 'items flex flex-column gap-5 align-center' : 'items openMenu flex flex-column gap-5 align-center'}`}>
              <span></span>
              <span></span>
              <span></span>
            </div>
          </div>
          <div ref={refElement} className={`${!openMenu ? "sidebar flex align-center justify-center" : "sidebar active flex align-center justify-center"}`}>
            <Sidebar handleMenu={(item: boolean) => setOpenMenu(item)}/>
          </div>
        </div>
      </div>
    </Style>
  );
};