import { PrimaryLayout } from "../../components/layouts/primaryLayout";
import { Description } from "./description";
import { EducationAndExperience } from "./education";
import { KnowledgeAndInterest } from "./interest";
import { Persona } from "./persona";
import { Skills } from "./skills";
import { Style } from "./style";

export const AboutPage: React.FC = () => {
  return (
    <PrimaryLayout title="About Hamed Qaradaqi">
      <Style>
        <div className="container">
          <div className="content">
            <div className="title">About Me</div>
            <div className="intro">
              <img className="img" src="/asset/image/bg1.jpeg" alt="Hamed Qaradaqi" title="Hamed Qaradaqi" />
              <div className="info">
                <h1 className="name">Hamed Qaradaqi</h1>
                <p className="profession">Mechanical Engineer, Web Developer, Tutor</p>
              </div>
              <Description />
              <Persona />
              <Skills />
              <KnowledgeAndInterest />
              <EducationAndExperience />
            </div>
          </div>
        </div>
      </Style>
    </PrimaryLayout>
  );
};