import { Style } from "./style";
import moment from "moment";

interface Item {
  id: number;
  title: string;
  content: string;
}

const firstCol: Item[] = [
  {
    id: 1,
    title: 'Birthday:',
    content: '23.10.1990'
  },
  {
    id: 2,
    title: 'Age:',
    content: `${moment().diff(moment('19901023', 'YYYYMMDD'), 'years')}`
  },
  {
    id: 3,
    title: 'Address:',
    content: 'Tehran, Iran',
  },
  {
    id: 4,
    title: 'Email:',
    content: 'hqaradaqi@gmail.com',
  },
  {
    id: 5,
    title: 'Phone:',
    content: '+98-910-405-8504',
  },
];
const secondCol: Item[] = [
  {
    id: 1,
    title: 'Nationality:',
    content: 'Iran'
  },
  {
    id: 2,
    title: 'Study:',
    content: "University of Tehran"
  },
  {
    id: 3,
    title: 'Degree:',
    content: 'MSc',
  },
  {
    id: 4,
    title: 'Interest:',
    content: 'Self-Improvement',
  },
  {
    id: 5,
    title: 'Freelance:',
    content: 'Available',
  },
];

export const Persona: React.FC = () => {
  const renderItems = (items: Item[]) => {
    return (
      items.map((item, idx) => (
        <li className="item flex align-center justify-start" key={idx}>
          <h3 className="item-title nowrap">{item.title}</h3>
          <p className="item-body nowrap">{item.content}</p>
        </li>
      ))
    );
  };
  return (
    <Style>
      <div className="persona flex align-start justify-start">
        <ul className="col flex flex-column align-start gap-10">
          {renderItems(firstCol)}
        </ul>
        <ul className="col flex flex-column align-start gap-10">
          {renderItems(secondCol)}
        </ul>
      </div>
    </Style>
  );
};