import styled from "styled-components";
import { pallete } from "../../theme";

export const Style = styled.div`
.content {
 padding: 25px 0;
 .title {
  font-size: 2.2rem;
  font-weight: 800;
  color: ${pallete.primaryColor};
  margin-bottom: 65px;
 }
 .intro {
  .img {
    width: 100%;
    height: 380px;
    overflow: hidden;
    object-fit: cover;
  }
  .info {
    padding: 32px 0;
    border-bottom: 1px solid ${pallete.primaryColor};
    .name {
      font-size: 1.8rem;
      font-weight: 700;
      color: ${pallete.uiColor};
      margin: 18px 0 8px;
    }
    .profession {
      font-size: 1.3rem;
      font-weight: 400;
      color: ${pallete.primaryColor};
      opacity: 0.6;
    }
  }
 }
}
@media only screen and (max-width:1200px) {
  padding: 0 25px;
}
@media only screen and (max-width:500px) {
  padding: 0;
  .content {
    .intro {
      .img {
        height: 250px;
      }
      .info {
        padding: 16px 0;
      }
    }
  }
}
`;