import styled from "styled-components";
import { pallete } from "../../../theme";

export const Style = styled.div`
padding: 65px 0;
border-bottom: 1px solid ${pallete.primaryColor};
.edu-exp {
  width: 100%;
  .col {
    padding: 8px 45px 8px 0;
    width: 50%;
    border-left: 2px solid ${pallete.primaryColor};
    padding-left: 25px;
    .col-title {
      font-size: 2.2rem;
      color: ${pallete.uiColor};
      font-weight: 800;
      padding: 8px 0 32px;
    }
    .item {
      width: 100%;
      padding: 12px 0;
      margin-bottom: 25px;
      .item-years {
        position: relative;
        z-index: 2;
        font-size: 1.4rem;
        padding: 14px;
        background-color: rgba(252, 252, 252, 0.25);
        border-radius: 40px;
        /* min-width: 150px; */
        margin-right: 15px;
        &::before {
          content: "";
          position: absolute;
          z-index: 1;
          top: 15.5px;
          left: -33px;
          width: 15px;
          height: 15px;
          border-radius: 50%;
          border: 2px solid ${pallete.primaryColor};
          background-color: ${pallete.uiColor};
          overflow: hidden;
        }
      }
      .item-info {
        .info-title {
          font-size: 1.8rem;
          color: ${pallete.primaryColor};
        }
        .info-body {
          font-size: 1.4rem;
          color: ${pallete.primaryColor};
          opacity: 0.6;
        }
      }
    }
  }
}
@media only screen and (max-width: 900px) {
  .edu-exp {
  width: 100%;
  flex-direction: column;
  gap: 20px;
  .col {
    width: 100%;
    padding: 8px 0px 8px 25px;
    .col-title {
      font-size: 2rem;
    }
    .item {
      .item-years {
        font-size: 1.3rem;
      }
      .item-info {
        .info-title {
          font-size: 1.6rem;
        }
        .info-body {
          font-size: 1.2rem;
        }
      }
    }
  }
}
}
`;