import styled from "styled-components";
import { pallete } from "../../../../theme";

interface Props {
  progress: number;
}

export const Style = styled.div<Props>`
width: 100%;
.item-progress {
  position: relative;
  height: 2px;
  width: 100%;
  background-color: rgba(252, 252, 252, 0.5);
  &::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    height: 2px;
    width: ${(props) => (`${props.progress}%`)};
    background-color: ${pallete.primaryColor};
    opacity: 0.7;
  }
}
`;