import { ConfigProvider, Progress } from "antd";
import { Style } from "./style";
import { pallete } from "../../../theme";
import { SkillProgress } from "./progress";

interface Item {
  id: number;
  title: string;
  percentage: number;
}
interface colItems {
  title: string;
  items: Item[];
}

const firstCol: colItems = {
  title: 'Programming Skills',
  items: [
    {
      id: 1,
      title: 'Python',
      percentage: 90,
    },
    {
      id: 2,
      title: 'Reactjs',
      percentage: 85,
    },
    {
      id: 3,
      title: 'JavaScript',
      percentage: 85,
    },
  ]
};
const secondCol: colItems = {
  title: 'Language Skills',
  items: [
    {
      id: 1,
      title: 'English',
      percentage: 80,
    },
    {
      id: 2,
      title: 'Persian',
      percentage: 100,
    },
    {
      id: 3,
      title: 'Azerbaijani',
      percentage: 100,
    },
  ]
};
export const Skills: React.FC = () => {
  const renderItems = (items: Item[]) => {
    return (
      items.map((item, idx) => (
        <div className="item flex flex-column align-start gap-10" key={idx}>
          <div className="item-info flex align-center justify-between">
            <p className="info-title">{item.title}</p>
            <p className="info-quantity">{item.percentage}%</p>
          </div>
          <SkillProgress progress={item.percentage} />
        </div>
      ))
    );
  };
  return (
    <Style >
      <div className="skills flex align-start justify-start">
        <div className="col">
          <div className='col-title'>{firstCol.title}</div>
          {renderItems(firstCol.items)}
        </div>
        <div className="col">
          <div className="col-title">{secondCol.title}</div>
          {renderItems(secondCol.items)}
        </div>
      </div>
    </Style>
  );
};