import { createGlobalStyle } from "styled-components";
import { pallete } from "./theme";

export const GlobalStyle = createGlobalStyle`
html {
  font-size: 10px;
}
*, *::before, *::after {
  box-sizing: border-box;
}
* {
  margin: 0;
  padding: 0;
}
body {
  background-color: ${pallete.backgroundColor};
  /* font-family: "Montserrat", sans-serif;
  font-optical-sizing: auto;
  font-weight: 600;
  font-style: normal; */
  font-family: "Sora", sans-serif;
  font-optical-sizing: auto;
  font-weight: 500;
  font-style: normal;
  color: ${pallete.primaryColor};
}
ul, ol {
  list-style: none;
}
a {
  text-decoration: none;
}
.container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 16px;
}
.flex {
  display: flex;
}
.grid {
  display: grid;
}
.flex-column {
  flex-direction: column;
}
.wrap {
  flex-wrap: wrap;
}
.no-wrap {
  flex-wrap: nowrap;
}
.align-start {
  align-items: start;
}
.align-center {
  align-items: center;
}
.align-end {
  align-items: end;
}
.justify-start {
  justify-content: start;
}
.justify-center {
  justify-content: center;
}
.justify-end {
  justify-content: end;
}
.justify-between {
  justify-content: space-between;
}
.justify-around {
  justify-content: space-around;
}
.justify-evenly {
  justify-content: space-evenly;
}
.gap-4 {
  gap: 4px;
}
.gap-5 {
  gap: 5px;
}
.gap-10 {
  gap: 10px;
}
.gap-16 {
  gap: 16px;
}
.gap-20 {
  gap: 20px;
}
.prl-4 {
  padding-right: 4px;
  padding-left: 4px;
}
.prl-5 {
  padding-right: 5px;
  padding-left: 5px;
}
.prl-8 {
  padding-right: 8px;
  padding-left: 8px;
}
.prl-10 {
  padding-right: 10px;
  padding-left: 10px;
}
.prl-16 {
  padding-right: 16px;
  padding-left: 16px;
}
.prl-20 {
  padding-right: 20px;
  padding-left: 20px;
}
.ptb-4 {
  padding-top: 4px;
  padding-bottom: 4px;
}
.ptb-5 {
  padding-top: 5px;
  padding-bottom: 5px;
}
.ptb-8 {
  padding-top: 8px;
  padding-bottom: 8px;
}
.ptb-10 {
  padding-top: 10px;
  padding-bottom: 10px;
}
.ptb-16 {
  padding-top: 16px;
  padding-bottom: 16px;
}
.ptb-20 {
  padding-top: 20px;
  padding-bottom: 20px;
}
`;