import styled from "styled-components";
import { pallete } from "../../../theme";

export const Style = styled.div`
padding: 65px 0;
border-bottom: 1px solid ${pallete.primaryColor};
.know-edu {
  width: 100%;
  .col {
    padding: 8px 45px 8px 0;
    width: 50%;
    .col-title {
      font-size: 2.2rem;
      color: ${pallete.uiColor};
      font-weight: 800;
      padding: 8px 0 32px;
    }
    .item {
      width: 100%;
      padding: 12px 0;
      .item-info {
        width: 100%;
        font-size: 1.4rem;
        color: ${pallete.primaryColor};
        opacity: 0.8;
      }
    }
  }
}
@media only screen and (max-width: 900px) {
  .know-edu {
    flex-direction: column;
    width: 100%;
  .col {
    width: 100%;
    .col-title {
      font-size: 2rem;
    }
  }
}
}
`;