import styled from "styled-components";
import { pallete } from "../../theme";

export const Style = styled.div`
.footer-content {
  padding: 15px 0;
  .footer-items {
    .item {
      .item-link {
        color: ${pallete.primaryColor};
        font-size: 2rem;
        padding: 5px;
        transition: all 0.3s ease-in-out;
        &:hover {
          color: ${pallete.uiColor};
        }
      }
    }
  }
  .email {
    font-size: 1.3rem;
    font-weight: 400;
    padding: 4px 0;
    color: ${pallete.primaryColor};
    transition: all 0.3s ease-in-out;
    background-color: ${pallete.uiColor};
    border-radius: 4px;
  }
}
`;