import { Link } from "react-router-dom";
import { PrimaryLayout } from "../../components/layouts/primaryLayout";
import { Style } from "./style";
import { pallete } from "../../theme";
import { useEffect, useState } from "react";

export const HomePage: React.FC = () => {
  const [width, setWidth] = useState<number>(window.innerWidth);
  useEffect(()=>{
    setWidth(window.innerWidth)
  }, [])
  return (
    <PrimaryLayout title="Hamed Qaradaqi">
      <Style>
        <div className="container">
          <div className="content">
            <ul className="items flex align-start justify-between">
              <li className="left-column">
                <ul className="left-items flex flex-column align-start gap-16">
                  <li className="left-items-title">
                    <h1>Hi, <br />I'm <b>Hamed Qaradaqi</b>
                    </h1>
                  </li>
                  <li className="left-items-body">
                    <p>Mechanical Engineer, Web Developer, Tutor</p>
                  </li>
                  <li className="left-items-btn">
                    <Link className="left-contact flex align-center justify-start gap-5" to={'/about'}>
                      <span>About Me</span>
                      <i className="fa-solid fa-arrow-right"></i>
                    </Link>
                  </li>
                </ul>
              </li>
              <li className="right-column flex align-start justify-start">
                <ul className="right-items">
                  <li className="right-items-title">
                    <span>Expert on</span>
                  </li>
                  <li className="right-items-body">
                    <div className="flex align-center justify-start gap-5">
                      <i className="fa-solid fa-check" style={{ color: pallete.uiColor }}></i>
                      <span>Mechanical Engineering</span>
                    </div>
                    <div className="flex align-center justify-start gap-5">
                      <i className="fa-solid fa-check" style={{ color: pallete.uiColor }}></i>
                      <span>Web Development</span>
                    </div>
                    <div className="flex align-center justify-start gap-5">
                      <i className="fa-solid fa-check" style={{ color: pallete.uiColor }}></i>
                      <span>Learning Programming</span>
                    </div>
                    <div className="flex align-center justify-start gap-5">
                      <i className="fa-solid fa-check" style={{ color: pallete.uiColor }}></i>
                      <span>Learning Physics, Mathematics</span>
                    </div>
                  </li>
                  <li className="right-items-sub-body">
                    <span>Hi, If you need an experienced mechanical engineer,  web developer, or want to learn programming in Python, Reactjs, and/or high-school Mathematics/Physics, I am perfect specialist in these fields. Let grow your business and/or skills together.</span>
                  </li>
                  <li className="right-items-btn">
                    <a className="cv-download flex align-center justify-start gap-16" href={'/asset/cv/cv.pdf'} download={'/asset/cv/cv.pdf'} rel="noreferrer" target="_blank">
                      <span>Download Resume</span>
                      <i className="fa-solid fa-download"></i>
                    </a>
                  </li>
                  {width < 900 && <li className="right-about-btn flex align-center justify-start">
                    <Link className="right-about flex align-center justify-start gap-5" to={'/about'}>
                      <span>About Me</span>
                      <i className="fa-solid fa-arrow-right"></i>
                    </Link>
                  </li>}
                </ul>
              </li>
            </ul>
          </div>
        </div>
      </Style>
    </PrimaryLayout>
  );
};