import styled from "styled-components";
import { pallete } from "../../theme";

export const Style = styled.div`
  background-image: url('/asset/image/bg3.jpg');
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: auto;
  min-height: calc(100vh - 174px);
.content {
  position: relative;
  z-index: 1;
  .items {
    height: 100%;
    width: 100%;
    padding: 100px 8px 50px;
    /* backdrop-filter: blur(6px); */
    .left-column {
      width: 35%;
      .left-items {
        width: 100%;
        .left-items-title {
          h1 {
            font-size: 2.8rem;
            color: ${pallete.primaryColor};
            font-weight: 700;
            b {
            color: ${pallete.uiColor};
            font-weight: 800;
          }
        }
      }
      .left-items-body {
          font-size: 1.8rem;
          font-weight: 600;
          color: ${pallete.primaryColor};
        }
        .left-items-btn {
          margin-top: 24px;
          .left-contact {
            flex-grow: 0;
            background-color: ${pallete.uiColor};
            color: ${pallete.primaryColor};
            padding: 12px 16px;
            font-size: 1.6rem;
            border-radius: 4px;
            transition: all 0.3s ease-in-out;
            &:hover {
              background-color: ${pallete.primaryColor};
              color: ${pallete.uiColor};
            }
          }
        }
      }
    }
    .right-column {
      width: 35%;
      .right-items {
        width: 100%;
        .right-items-title {
          color: ${pallete.uiColor};
          font-size: 1.4rem;
          font-weight: 700;
        }
        .right-items-body {
          font-size: 1.4rem;
          font-weight: 500;
          color: ${pallete.primaryColor};
          line-height: 1.75;
          margin: 25px 0;
        }
        .right-items-sub-body {
          font-size: 1.2rem;
          font-weight: 400;
          color: ${pallete.primaryColor};
          line-height: 1.75;
          margin: 0 8px 12px 0;
          text-indent: 10px;
        }
        .right-items-btn {
          .cv-download {
            font-size: 1.4rem;
            color: ${pallete.uiColor};
            margin-top: 32px;
          }
        }
        .right-about-btn {
          margin-top: 24px;
          .right-about {
            background-color: ${pallete.uiColor};
            color: ${pallete.primaryColor};
            padding: 12px 16px;
            font-size: 1.6rem;
            border-radius: 4px;
            transition: all 0.3s ease-in-out;
            &:hover {
              background-color: ${pallete.primaryColor};
              color: ${pallete.uiColor};
            }
          }
        }
      }
    }
  }
}
@media screen and (max-width: 900px) {
  background-image: url('/asset/image/bg1.jpeg');
  position: relative;
  .content {
    .items {
      padding: 50px 8px;
      flex-direction: column;
      gap: 25px;
      .left-column {
        width: 100%;
        .left-items {
          .left-items-title {
          h1 {
            font-size: 2.2rem;
        }
      }
      .left-items-body {
        font-size: 1.6rem;
      }
      .left-items-btn {
        display: none;
      }
    }
    }
    .right-column {
      width: 100%;
    }
  }
  }
  &::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  backdrop-filter: blur(4px);
  background-color: rgba(53, 53, 53, 0.25);
}
}
`;