import { Footer } from "../../footer";
import { HelmetComp } from "../../helmet";
import { Navbar } from "../../navbar";

interface Props {
  children: React.ReactNode,
  title: string;
}

export const PrimaryLayout: React.FC<Props> = ({ children, title }) => {
  return (
    <>
      <HelmetComp title={title} />
      <Navbar />
      {children}
      <Footer />
    </>
  );
};