import { Link } from "react-router-dom";
import { Style } from "./style";

interface Props {
  handleMenu: (item: boolean) => void;
}

interface Items {
  id: number;
  link: string;
  name: string;
  icon: string;
}

const sideItems: Items[] = [
  {
    id: 1,
    name: "Home",
    link: '/',
    icon: 'fa-solid fa-home',
  },
  {
    id: 2,
    name: "About",
    link: '/about',
    icon: 'fa-solid fa-info-circle',
  },
];

export const Sidebar: React.FC<Props> = ({ handleMenu }) => {
  
  const renderFarm = () => {
    return (
      sideItems.map((item, idx) => (
        <li key={idx} className="item flex align-center justify-start" onClick={() => handleMenu(false)}>
          <Link className="item-link flex align-center justify-start gap-5" to={item.link}>
            <i className={item.icon}></i>
            <span>{item.name}</span>
          </Link>
        </li>
      ))
    );
  };
  return (
    <Style>
      <div className="container">
        <div className="content">
          <ul className="items flex flex-column align-start gap-16">
            {renderFarm()}
          </ul>
        </div>
      </div>
    </Style>
  );
};