import { Style } from "./style";


interface Item {
  id: number;
  title: string;
}
interface colItems {
  title: string;
  items: Item[];
}

const firstCol: colItems = {
  title: 'Knowledge',
  items: [
    {
      id: 1,
      title: 'Python, JavaScript, Matlab',
    },
    {
      id: 2,
      title: 'Reactjs, Django, Flask',
    },
    {
      id: 3,
      title: 'DRF, Antd, StyledComponents',
    },
    {
      id: 4,
      title: 'Ansys Fluent, Ansys CFX, Catia',
    },
    {
      id: 5,
      title: 'Numpy, Pandas, Matplotlib, SQL',
    },
  ]
};
const secondCol: colItems = {
  title: 'Interests',
  items: [
    {
      id: 1,
      title: 'Research and Development',
    },
    {
      id: 2,
      title: 'Create Website',
    },
    {
      id: 3,
      title: 'Data Analysis',
    },
    {
      id: 4,
      title: 'Teach Physics and Mathematics',
    },
    {
      id: 5,
      title: 'Feasibility Study',
    },
  ]
};
export const KnowledgeAndInterest: React.FC = () => {
  const renderItems = (items: Item[]) => {
    return (
      items.map((item, idx) => (
        <div className="item flex flex-column align-start gap-10" key={idx}>
          <div className="item-info flex align-center justify-start gap-10">
            <i className="fa-solid fa-caret-right"></i>
            <p className="info-title">{item.title}</p>
          </div>
        </div>
      ))
    );
  };
  return (
    <Style >
      <div className="know-edu flex align-start justify-start">
        <div className="col">
          <div className='col-title'>{firstCol.title}</div>
          {renderItems(firstCol.items)}
        </div>
        <div className="col">
          <div className="col-title">{secondCol.title}</div>
          {renderItems(secondCol.items)}
        </div>
      </div>
    </Style>
  );
};