import styled from "styled-components";
import { pallete } from "../../theme";

export const Style = styled.div`
background-color: transparent;
.content {
  padding: 5px 0;
  position: relative;
  .logo {
    .image {
      padding: 4px;
      .img {
        height: 70px;
        width: auto;
      }
    }
  }
  .hamburger-menu {
    cursor: pointer;
    .items {
      width: 25px;
      height: 15px;
      position: relative;
      margin: 2px auto;
      transform: rotate(0deg);
      transition: .5s ease-in-out;
      span {
        display: block;
        position: absolute;
        height: 2px;
        width: 100%;
        background: ${pallete.primaryColor};
        border-radius: 2px;
        opacity: 1;
        left: 0;
        transform: rotate(0deg);
        transition: .25s ease-in-out;
        &:span:nth-child(1) {
          top: 0px;
        }
        &:nth-child(2) {
          top: 8px;
        }
        &:nth-child(3) {
          top: 16px;
        }
    }
    &.openMenu {
      span:nth-child(1) {
        top: 12px;
        transform: rotate(135deg);
      }
      span:nth-child(2) {
        opacity: 0;
        left: -60px;
      }
      span:nth-child(3) {
        top: 12px;
        transform: rotate(-135deg);
      }
    }
    }
  }
  .sidebar {
  position: fixed;
  z-index: 2;
  left: 0;
  bottom: -50px;
  width: 250px;
  height: 0;
  backdrop-filter: blur(16px);
  visibility: hidden;
  opacity: 0;
  transition: all 0.7s ease-in-out;
  &.active {
    bottom: 0;
    height: 100%;
    visibility: visible;
    opacity: 1;
    transition: all 0.7s ease-in-out;
  }
}
}
@media only screen and (max-width: 900px) {
  .content {
    .logo {
    .image {
      padding: 4px;
      .img {
        height: 50px;
        width: auto;
      }
    }
  }
  }
}
`;