import { Style } from "./style";

interface Item {
  id: number;
  years: string;
  organization: string;
  position: string;
}
interface colItems {
  title: string;
  items: Item[];
}

const firstCol: colItems = {
  title: 'Education',
  items: [
    {
      id: 1,
      years: '2014-2016',
      organization: 'University of Tehran',
      position: 'MSc, Mechanical Engineering',
    },
    {
      id: 2,
      years: '2009-2014',
      organization: 'University of Tabriz',
      position: 'BSc, Mechanical Engineering',
    },
  ]
};
const secondCol: colItems = {
  title: 'Interests',
  items: [
    {
      id: 1,
      years: '2023-2024',
      organization: 'Freelance',
      position: 'Web Developer, Data Analyst, Feasibility Study',
    },
    {
      id: 2,
      years: '2018-2023',
      organization: 'Karun Petrochemical Co.',
      position: 'Research and Development Specialist / Data Analyst',
    },
  ]
};

export const EducationAndExperience: React.FC = () => {
  const renderItems = (items: Item[]) => {
    return (
      items.map((item, idx) => (
        <div className="item flex align-center justify-start gap-10" key={idx}>
          <div className="item-years flex align-center justify-center">
            <span>{item.years}</span>
          </div>
          <div className="item-info flex flex-column align-start gap-10">
            <p className="info-title">{item.organization}</p>
            <p className="info-body">{item.position}</p>
          </div>
        </div>
      ))
    );
  };
  return (
    <Style>
      <div className="edu-exp flex align-start justify-start">
        <div className="col">
          <div className='col-title'>{firstCol.title}</div>
          {renderItems(firstCol.items)}
        </div>
        <div className="col">
          <div className="col-title">{secondCol.title}</div>
          {renderItems(secondCol.items)}
        </div>
      </div>
    </Style>
  );
};